<template>
  <div class="btns">
    <input
      v-for="(button, index) in displayButtons"
      :key="button.id || index"
      type="button"
      :value="button.label"
      :disabled="button.disabled"
      @click="handleButtonClick(button.action)"
    />
  </div>
</template>

<script>
import eventBus from "../utils/eventBus.js";

export default {
  name: "GameControls",
  props: {
    // ‰ΩøÁî®Êï∞ÁªÑÂÆö‰πâÊåâÈíÆÈÖçÁΩÆÔºå‰ºòÂÖàÁ∫ßÈ´ò‰∫éÂçïÁã¨ÁöÑshowÂ±ûÊÄß
    buttons: {
      type: Array,
      default: () => [],
    },
    // ÁªÑ‰ª∂ÂîØ‰∏ÄÊ†áËØÜÔºåÁî®‰∫é‰∫ã‰ª∂ÊÄªÁ∫øÈÄö‰ø°
    instanceId: {
      type: String,
      default: () => `gameControls_${Math.random().toString(36).substr(2, 9)}`,
    },

    // ‰ª•‰∏ãÂ±ûÊÄß‰øùÊåÅÂêëÂêéÂÖºÂÆπÊÄß
    showUndo: {
      type: Boolean,
      default: true,
    },
    showRestart: {
      type: Boolean,
      default: true,
    },
    showStep: {
      type: Boolean,
      default: true,
    },
    showAuto: {
      type: Boolean,
      default: true,
    },

    undoDisabled: {
      type: Boolean,
      default: false,
    },
    restartDisabled: {
      type: Boolean,
      default: false,
    },
    stepDisabled: {
      type: Boolean,
      default: false,
    },
    autoDisabled: {
      type: Boolean,
      default: false,
    },
    isAutoRunning: {
      type: Boolean,
      default: false,
    },
  },
  
  // ‰ΩøÁî®watchÁõëÂê¨ÊåâÈíÆÈÖçÁΩÆÂèòÂåñ
  watch: {
    displayButtons: {
      handler(newButtons) {
        // Âêë‰∫ã‰ª∂ÊÄªÁ∫øÂπøÊí≠ÊåâÈíÆÈÖçÁΩÆ
        eventBus.emit('game-controls:buttons-updated', {
          instanceId: this.instanceId,
          buttons: newButtons
        });
      },
      deep: true,
      immediate: true
    }
  },
  
  computed: {
    // Ê†πÊçÆbuttonsÊï∞ÁªÑÊàñÂçïÁã¨ÁöÑshowÂ±ûÊÄßÁîüÊàêË¶ÅÊòæÁ§∫ÁöÑÊåâÈíÆÂàóË°®
    displayButtons() {
      // Â¶ÇÊûúÊèê‰æõ‰∫ÜbuttonsÊï∞ÁªÑÔºåÂàô‰ΩøÁî®ÂÆÉ
      if (this.buttons && this.buttons.length > 0) {
        return this.buttons;
      }

      // Âê¶Âàô‰ΩøÁî®ÂçïÁã¨ÁöÑshowÂ±ûÊÄßÁîüÊàêÈªòËÆ§ÊåâÈíÆÂàóË°®Ôºà‰øùÊåÅÂêëÂêéÂÖºÂÆπÔºâ
      const defaultButtons = [];

      if (this.showUndo) {
        defaultButtons.push({
          label: "‚óÄÔ∏é",
          action: "undo",
          disabled: this.undoDisabled,
        });
      }

      if (this.showRestart) {
        defaultButtons.push({
          label: "RESTART",
          action: "goon",
          disabled: this.restartDisabled,
        });
      }

      if (this.showAuto) {
        defaultButtons.push({
          label: this.isAutoRunning ? "STOP" : "AUTO",
          action: "auto",
          disabled: this.autoDisabled,
        });
      }

      if (this.showStep) {
        defaultButtons.push({
          label: "‚ñ∫",
          action: "step",
          disabled: this.stepDisabled,
        });
      }

      return defaultButtons;
    },
  },
  methods: {
    // Áªü‰∏ÄÁöÑÊåâÈíÆÁÇπÂáª‰∫ã‰ª∂Â§ÑÁêÜ
    handleButtonClick(action) {
      this.$emit(action);
    },

    // ‰øùÁïôÂéüÊúâÊñπÊ≥ï‰ª•‰øùÊåÅÂÖºÂÆπÊÄß
    undo() {
      this.$emit("undo");
    },
    goon() {
      this.$emit("goon");
    },
    stepFn() {
      this.$emit("step");
    },
    pass() {
      this.$emit("auto");
    },
  },
  
  mounted() {
    // ÁªÑ‰ª∂ÊåÇËΩΩÊó∂ÈÄöÁü•‰∫ã‰ª∂ÊÄªÁ∫ø
    eventBus.emit('game-controls:mounted', {
      instanceId: this.instanceId,
      buttons: this.displayButtons
    });
  },
  
  beforeUnmount() {
    // ÁªÑ‰ª∂Âç∏ËΩΩÊó∂ÈÄöÁü•‰∫ã‰ª∂ÊÄªÁ∫ø
    eventBus.emit('game-controls:unmounted', {
      instanceId: this.instanceId
    });
  },
};
</script>

<style scoped>
@import url("./sum.css");

/* ‰øùÊåÅ‰∏éÂéüÊ†∑Âºè‰∏ÄËá¥ÁöÑÊåâÈíÆÂÆπÂô®Ê†∑Âºè */
.btns {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
  padding: 0.25rem 0;
  flex-wrap: wrap;
}

/* Ë¶ÜÁõñÂéüÊúâinputÊ†∑Âºè‰ª•Á°Æ‰øùÊåâÈíÆ‰∏ÄËá¥ÊÄß */
.btns input {
  margin: 0.5rem 0;
  color: #3c4245;
  padding: 0.25rem 0.625rem;
  border: 0.0625rem solid #dfcdc3;
  background-color: #dfcdc3;
  cursor: pointer;
  font-size: 0.875rem;
}

.btns input:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
</style>
