<template>
  <transition>
    <div v-if="true" :class="['lose', customClass]" :style="computedModalStyle">
      <h1 v-if="title">{{ title }}</h1>
      <h1 v-if="subtitle" class="small">{{ subtitle }}</h1>

      <!-- Áî®‰∫éÊòæÁ§∫È¢ùÂ§ñÂÜÖÂÆπÁöÑÊèíÊßΩ -->
      <slot name="content"></slot>

      <!-- Áî®‰∫éÊòæÁ§∫Âç°ÁâáÁöÑÊèíÊßΩ -->
      <slot name="cards"></slot>

      <!-- ‰ΩøÁî® GameControls ÁªÑ‰ª∂Êõø‰ª£ÊâãÂä®ÊåâÈíÆÊ∏≤Êüì -->
      <GameControls
        :buttons="gameControlsButtons"
        @undo="handleAction('undo')"
        @goon="handleAction('goon')"
        @step="handleAction('step')"
        @auto="handleAction('auto')"
      />
       
       <!-- Â∫ïÈÉ®Á©∫ÁôΩÂå∫ÂüüÔºåÈ´òÂ∫¶Á≠â‰∫éfooterHeight -->
       <!-- <div class="modal-footer-spacer" :style="spacerStyle"></div> -->
    </div>
  </transition>
</template>

<script>
import GameControls from "./GameControls.vue";

export default {
  name: "GameResultModal",
  components: {
    GameControls
  },
  props: {
    title: {
      type: String,
      default: "",
    },
    subtitle: {
      type: String,
      default: "",
    },
    buttons: {
      type: Array,
      default: () => [],
    },
    modalStyle: {
      type: Object,
      default: () => ({}),
    },
    customClass: {
      type: String,
      default: "",
    },
    footerHeight: {
      type: Number,
      default: 50, // ÈªòËÆ§50pxÔºåËΩ¨Êç¢‰∏∫remÊòØ3.125rem
    },
  },
  computed: {
    computedModalStyle() {
      return {
        ...this.modalStyle,
        paddingBottom: `${this.footerHeight / 16}rem`
      };
    },
    spacerStyle() {
      return {
        height: `${this.footerHeight / 16}rem`
      };
    },
    gameControlsButtons() {
      // Â∞ÜÂéüÊúâÁöÑÊåâÈíÆÈÖçÁΩÆËΩ¨Êç¢‰∏∫ GameControls ÊâÄÈúÄÁöÑÊ†ºÂºè
      return this.buttons.map(button => ({
        label: button.text,
        action: button.callback.name || button.text.toLowerCase(),
        disabled: button.disabled,
        callback: button.callback
      }));
    }
  },
  methods: {
    handleAction(action) {
      // Ê†πÊçÆÂä®‰ΩúÁ±ªÂûãÊâæÂà∞ÂØπÂ∫îÁöÑÊåâÈíÆÂπ∂ÊâßË°åÂÖ∂ÂõûË∞É
      const button = this.buttons.find(btn => {
        const actionName = btn.callback.name || btn.text.toLowerCase();
        return actionName === action;
      });
      
      if (button && button.callback) {
        button.callback();
      }
    }
  }
};
</script>

