// ÁÆÄÂçïÁöÑ‰∫ã‰ª∂ÊÄªÁ∫øÂÆûÁé∞
class EventBus {
  constructor() {
    this.events = {};
  }

  // ËÆ¢ÈòÖ‰∫ã‰ª∂
  on(event, callback) {
    if (!this.events[event]) {
      this.events[event] = [];
    }
    this.events[event].push(callback);
  }

  // ÂèñÊ∂àËÆ¢ÈòÖ
  off(event, callback) {
    if (!this.events[event]) return;
    
    if (callback) {
      // ÁßªÈô§ÁâπÂÆöÂõûË∞É
      this.events[event] = this.events[event].filter(cb => cb !== callback);
    } else {
      // ÁßªÈô§ÊâÄÊúâÂõûË∞É
      delete this.events[event];
    }
  }

  // ÂèëÂ∏É‰∫ã‰ª∂
  emit(event, ...args) {
    if (!this.events[event]) return;
    
    this.events[event].forEach(callback => {
      callback(...args);
    });
  }
}

// ÂØºÂá∫Âçï‰æãÂÆû‰æã
export default new EventBus();